<template>
  <v-container
    fill-height
    fluid
  >
    <v-row
      dense
      align="center"
      justify="center"
      v-if="step === 1"
    >

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="12" sm="10">
        <h2 style="padding: 0 0 24px 0;"> Flight Planner </h2>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="12" sm="10">
        <v-select
          outlined
          :items="aircraft"
          label="Select an Aircraft"
          prepend-inner-icon="mdi-airplane"
          v-model="selectedAircraft"
        ></v-select>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5">
        <v-autocomplete
          outlined
          :items="airports"
          label="Where from?"
          clearable
          prepend-inner-icon="mdi-map-marker"
          v-model="homeData.airportCode"
          @change="attemptToPopulateData('homeData')"
        ></v-autocomplete>
      </v-col>
      <v-col cols="6" sm="5">
        <v-autocomplete
          outlined
          :items="airports"
          label="Where to?"
          clearable
          prepend-inner-icon="mdi-map-marker"
          v-model="destData.airportCode"
          @change="attemptToPopulateData('destData')"
        ></v-autocomplete>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5">
        <v-menu
          v-model="dateMenu"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="date"
              label="What day?"
              prepend-inner-icon="mdi-calendar"
              readonly
              outlined
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="date"
            @input="dateMenu = false">
          </v-date-picker>
        </v-menu>
      </v-col>
      <v-col cols="6" sm="5">
        <v-menu
          ref="menu"
          v-model="timeMenu"
          :close-on-content-click="false"
          :nudge-right="40"
          :return-value.sync="time"
          transition="scale-transition"
          offset-y
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="time"
              outlined
              label="What time?"
              prepend-inner-icon="mdi-clock-outline"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-time-picker
            v-if="timeMenu"
            v-model="time"
            full-width
            @click:minute="$refs.menu.save(time)"
          ></v-time-picker>
        </v-menu>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5"></v-col>
      <v-col cols="6" sm="5">
        <v-btn
          block
          color="primary"
          :disabled="!selectedAircraft"
          v-on:click="next"
          large
        >Next</v-btn>
      </v-col>
      <v-col cols="0" sm="1"></v-col>
    </v-row>

    <v-row
        align="center"
        justify="center"
        v-if="step === 2"
      >

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="12" sm="10">
        <h2> Flight Planner </h2>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5">
        <h3> Takeoff </h3>
      </v-col>
      <v-col cols="6" sm="5">
        <h3> Landing </h3>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5">
        <v-text-field
          v-model="homeData.temperature"
          outlined
          label="Temperature (Celsius)"
          prepend-inner-icon="mdi-thermometer"
          hide-details
          type="number"
        />
      </v-col>
      <v-col cols="6" sm="5">
        <v-text-field
          v-model="destData.temperature"
          outlined
          label="Temperature (Celsius)"
          prepend-inner-icon="mdi-thermometer"
          hide-details
          type="number"
        />
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5">
        <v-text-field
          v-model="homeData.elevation"
          outlined
          label="Elevation (meters)"
          prepend-inner-icon="mdi-elevation-rise"
          hide-details
          type="number"
        />
      </v-col>
      <v-col cols="6" sm="5">
        <v-text-field
          v-model="destData.elevation"
          outlined
          label="Elevation (meters)"
          prepend-inner-icon="mdi-elevation-rise"
          hide-details
          type="number"
        />
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5">
        <v-text-field
          v-model="homeData.windSpeed"
          outlined
          label="Wind Speed (kt)"
          prepend-inner-icon="mdi-weather-windy"
          hide-details
          type="number"
        />
      </v-col>
      <v-col cols="6" sm="5">
        <v-text-field
          v-model="destData.windSpeed"
          outlined
          label="Wind Speed (kt)"
          prepend-inner-icon="mdi-weather-windy"
          hide-details
          type="number"
        />
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5">
        <v-text-field
          v-model="homeData.windDirection"
          outlined
          label="Wind Direction (degrees)"
          prepend-inner-icon="mdi-compass-outline"
          hide-details
          type="number"
        />
      </v-col>
      <v-col cols="6" sm="5">
        <v-text-field
          v-model="destData.windDirection"
          outlined
          label="Wind Direction (degrees)"
          prepend-inner-icon="mdi-compass-outline"
          hide-details
          type="number"
        />
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5">
        <v-text-field
          v-model="homeData.localPressure"
          outlined
          label="Local Pressure (in. Hg)"
          prepend-inner-icon="mdi-altimeter"
          hide-details
          type="number"
        />
      </v-col>
      <v-col cols="6" sm="5">
        <v-text-field
          v-model="destData.localPressure"
          outlined
          label="Local Pressure (in. Hg)"
          prepend-inner-icon="mdi-altimeter"
          hide-details
          type="number"
        />
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5">
        <v-btn
          block
          color="primary"
          v-on:click="previous"
          large
        >Previous</v-btn>
      </v-col>
      <v-col cols="6" sm="5">
        <v-btn
          block
          color="primary"
          v-on:click="next"
          large
        >Next</v-btn>
      </v-col>
      <v-col cols="0" sm="1"></v-col>
    </v-row>
  </v-container>

</template>

<script>

import axios from 'axios'
axios.defaults.withCredentials = false

export default {
  name: 'Plan',

  data: () => ({
    step: 1,
    dateMenu: false,
    timeMenu: false,
    date: undefined,
    time: undefined,
    aircraft: ['Plane1', 'Plane2'],
    airports: [],
    selectedAircraft: undefined,
    homeData: {
      airportCode: undefined,
      elevation: undefined,
      temperature: undefined,
      windSpeed: undefined,
      windDirection: undefined,
      localPressure: undefined
    },
    destData: {
      airportCode: undefined,
      elevation: undefined,
      temperature: undefined,
      windSpeed: undefined,
      windDirection: undefined,
      localPressure: undefined
    }
  }),

  created: function () {
    this.loadAirportCodes()
  },

  methods: {

    next: function () {
      this.step++
    },

    previous: function () {
      this.step--
    },

    parseAirportCodes: function (data) {
      const codes = []
      const xml = data.getElementsByTagName('Station')
      for (let i = 0; i < xml.length; i++) {
        const stationXml = xml[i]
        const stationId = stationXml.getElementsByTagName('station_id')[0].innerHTML
        const stationSite = stationXml.getElementsByTagName('site')[0].innerHTML
        const stationState = stationXml.getElementsByTagName('state')[0].innerHTML
        codes.push({
          value: stationId,
          text: stationSite + ', ' + stationState + ' (' + stationId + ')'
        })
      }
      return codes
    },

    loadAirportCodes: function () {
      fetch('https://cors-anywhere.herokuapp.com/https://www.aviationweather.gov/adds/dataserver_current/httpparam?dataSource=stations&requestType=retrieve&format=xml&stationString=~us', {
        method: 'GET',
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Content-Type': 'text/xml'
        }
      })
        .then(response => response.text())
        .then(str => (new window.DOMParser()).parseFromString(str, 'text/xml'))
        .then(data => {
          const response = this.parseAirportCodes(data)
          this.airports = response
        })
    },

    parseMetarXml: function (data) {
      const json = {}
      const xml = data.getElementsByTagName('METAR')[0]
      if (!xml) {
        return json
      }
      json.latitude = Number(xml.getElementsByTagName('latitude')[0].innerHTML)
      json.longitude = Number(xml.getElementsByTagName('longitude')[0].innerHTML)
      json.tempC = Number(xml.getElementsByTagName('temp_c')[0].innerHTML)
      json.windSpeedKt = Number(xml.getElementsByTagName('wind_speed_kt')[0].innerHTML)
      json.elevationM = Number(xml.getElementsByTagName('elevation_m')[0].innerHTML)
      json.windDirDegrees = Number(xml.getElementsByTagName('wind_dir_degrees')[0].innerHTML)
      json.observationTime = new Date(xml.getElementsByTagName('observation_time')[0].innerHTML)
      json.altimInHg = Number(xml.getElementsByTagName('altim_in_hg')[0].innerHTML)
      return json
    },

    attemptToPopulateData: function (dataset) {
      this[dataset].temperature = undefined
      this[dataset].elevation = undefined
      this[dataset].windSpeed = undefined
      this[dataset].windDirection = undefined
      this[dataset].localPressure = undefined
      fetch('https://cors-anywhere.herokuapp.com/https://www.aviationweather.gov/adds/dataserver_current/httpparam?dataSource=metars&requestType=retrieve&format=xml&stationString=' + this[dataset].airportCode + '&hoursBeforeNow=12&mostRecent=true', {
        method: 'GET',
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Content-Type': 'text/xml'
        }
      })
        .then(response => response.text())
        .then(str => (new window.DOMParser()).parseFromString(str, 'text/xml'))
        .then(data => {
          if (data !== undefined) {
            const response = this.parseMetarXml(data)
            if (Object.keys(response).length > 0) {
              this[dataset].temperature = response.tempC
              this[dataset].elevation = response.elevationM
              this[dataset].windSpeed = response.windSpeedKt
              this[dataset].windDirection = response.windDirDegrees
              this[dataset].localPressure = response.altimInHg
            }
          }
        })
    }

  }

}
</script>

<style scoped>
  .container--fluid {
    height: 100%;
    padding: 0;
  }
  .col-0 {
    padding: 0;
  }
</style>
